.container {
  position: relative;
}

.header {
  background: linear-gradient(219.41deg, #e84d70 -125.98%, #a337f6 -0.14%, #28a7ed 111.06%);
  height: 75px;
  width: 100%;
  padding-inline: 1.5rem;
  display: grid;
  grid-auto-flow: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 0.5em;
}

.aside-wrapper {
  position: absolute;
  top: var(--header-height);
  right: 0;
  bottom: 0;
  overflow: hidden;
  display: grid;
  grid-template-columns: 0fr;
  background-color: hsla(0deg, 0%, 0%, 0.4);
}
.aside-wrapper[data-menu-active=true] {
  grid-template-columns: 1fr;
}
.aside-wrapper[data-menu-active=true] > .aside {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.aside-wrapper[data-menu-active=false] {
  grid-template-columns: 0fr;
}
.aside-wrapper[data-menu-active=false] > .aside {
  padding-inline: 0rem;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.aside {
  -webkit-transition: -webkit-transform 370ms;
  transition: -webkit-transform 370ms;
  transition: transform 370ms;
  transition: transform 370ms, -webkit-transform 370ms;
  background-color: var(--clr-grey-3);
  margin-left: auto;
  width: 70%;
  padding-block: 1.5rem;
  padding-inline: 1.5rem;
  overflow: hidden;
  display: grid;
  -ms-flex-line-pack: start;
      align-content: start;
  gap: 1.5rem;
}

.tag-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 0.98rem 0.5rem;
  margin: 0;
  background-color: var(--clr-white);
}
.tag-list li[data-active=true] > button {
  background-color: var(--clr-blue-5);
  color: var(--clr-white);
}

.roadmap {
  background: var(--clr-white);
}
.roadmap-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.roadmap__statuses {
  display: grid;
  gap: 0.75rem;
  padding: 0;
}
.roadmap__status {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.suggestion-list {
  display: grid;
  gap: 1rem;
  max-width: 90%;
  margin-inline: auto;
  padding: 0;
  padding-block: 2rem;
}

.suggestion {
  background-color: var(--clr-white);
  display: grid;
  gap: 1rem;
}
.suggestion:where(:hover, :focus-within) h2 {
  color: var(--clr-blue-5);
}
.suggestion-info {
  grid-row: 1;
  display: grid;
  gap: 0.5em;
  justify-items: start;
}
.suggestion .votes-counter {
  grid-row: 2;
  grid-column: 1;
  justify-self: start;
}
.suggestion .comment-wrapper {
  grid-row: 2;
  grid-column: 2;
  justify-self: end;
}

.comment-wrapper {
  display: grid;
  grid-auto-flow: column;
  gap: 0.5em;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.comment-wrapper .comment-count {
  color: var(--clr-dark-blue-5);
}
.suggestion-controler {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: auto -webkit-max-content;
  grid-template-columns: auto max-content;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 1rem;
}
.suggestion-controler .suggestion-count {
  display: none;
}
.suggestion-controler .sort-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  gap: 0.5rem;
}
.suggestion-controler .sort-menu button {
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.icon-suggestions {
  display: none;
}